body {
  background-color: #303240;
  color: #b84a0a;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.game {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .game-container {
    position: relative;
    .history {
      margin: 0 2em;
      position: absolute;
      top: 0;
      left: 100%;
      td {
        padding: 2px 4px;
      }
      .current-move {
        background-color: #b84a0a;
        color: #f0ddb2;
      }
    }
  }
  .game-data-input {
    display: flex;
    flex-direction: column;
    margin: 10px;
  }
  .controls {
    display: flex;
    margin: 1rem;
    justify-content: space-between;
  }
  .title {
    text-align: center;
  }
  .board {
    display: flex;
    flex-direction: column-reverse;
  }
  .rank {
    display: flex;
  }
  .square {
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    height: 60px;
    width: 60px;
    &.light{
      background-color: #f0ddb2;
    }
    &.dark {
      background-color: #b8895f;
    }
    .control-overlay {
      position: relative;
      width: 100%;
      height: 100%;
    }
    .piece {
      position: absolute;
    }
  }
}
